import { 
  getTopAnime,      // Fungsi ini sekarang tanpa parameter
  getSeasonNow, 
  getPopularAnime, 
  Anime 
} from '@/lib/api'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { Button } from '@/components/ui/button'
import Link from 'next/link'
import { ArrowRight, Play, Star, TrendingUp } from 'lucide-react'
import Image from 'next/image'

export default async function Home() {
  // --- PERBAIKAN DISINI ---
  // Hapus angka 1 dari getTopAnime() karena sekarang otomatis ambil 50 data
  const topAnimeData = await getTopAnime() 
  const seasonAnimeData = await getSeasonNow()
  const popularAnimeData = await getPopularAnime()

  const heroAnime = topAnimeData.data[0]
  const topAnimeList = topAnimeData.data.slice(0, 10) // Ambil 10 untuk list samping
  const seasonalAnime = seasonAnimeData.data.slice(0, 12) // Ambil 12 untuk grid
  const popularAnime = popularAnimeData.data.slice(0, 12) // Ambil 12 untuk grid

  // Hero Background Image (Safe Fallback)
  const heroImage = heroAnime?.images?.jpg?.large_image_url || '/placeholder.svg'

  return (
    <main className="min-h-screen bg-background overflow-x-hidden">
      <Navbar />

      {/* HERO SECTION */}
      {heroAnime && (
        <section className="relative h-[500px] md:h-[700px] w-full flex items-end pb-12 md:pb-24">
          {/* Background Image & Gradient */}
          <div className="absolute inset-0 -z-10">
            <Image
              src={heroImage}
              alt={heroAnime.title}
              fill
              className="object-cover blur-[2px] brightness-[0.6]"
              priority
            />
            <div className="absolute inset-0 bg-gradient-to-t from-background via-background/40 to-transparent" />
            <div className="absolute inset-0 bg-gradient-to-r from-background/80 via-transparent to-transparent" />
          </div>

          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 w-full grid grid-cols-1 md:grid-cols-12 gap-8 items-end">
            {/* Hero Info */}
            <div className="md:col-span-7 lg:col-span-8 space-y-4 animate-in slide-in-from-left-10 duration-700 fade-in">
              <span className="text-primary font-bold tracking-wider text-sm uppercase bg-primary/10 px-3 py-1 rounded-full border border-primary/20">
                #{heroAnime.rank || 1} Trending Now
              </span>
              <h1 className="text-4xl md:text-6xl font-extrabold text-white leading-tight line-clamp-2 drop-shadow-lg">
                {heroAnime.title}
              </h1>
              <p className="text-gray-200 text-lg line-clamp-2 max-w-2xl drop-shadow-md">
                {heroAnime.synopsis}
              </p>
              
              <div className="flex flex-wrap items-center gap-3 pt-2">
                <Link href={`/anime/${heroAnime.mal_id}`}>
                  <Button size="lg" className="gap-2 bg-primary hover:bg-primary/90 text-white font-bold shadow-lg shadow-primary/25">
                    <Play fill="currentColor" size={18} /> Watch Now
                  </Button>
                </Link>
                <Link href={`/anime/${heroAnime.mal_id}`}>
                  <Button size="lg" variant="outline" className="gap-2 border-white/30 text-white hover:bg-white/10 backdrop-blur-sm">
                    Details
                  </Button>
                </Link>
              </div>
            </div>

            {/* Top 5 Sidebar (Desktop Only) */}
            <div className="hidden md:block md:col-span-5 lg:col-span-4 bg-black/40 backdrop-blur-md border border-white/10 rounded-xl p-5 animate-in slide-in-from-right-10 duration-700 fade-in">
              <h3 className="text-white font-bold text-lg mb-4 flex items-center gap-2">
                <TrendingUp className="text-primary" /> Top Trending
              </h3>
              <div className="flex flex-col gap-4">
                {topAnimeList.slice(1, 6).map((anime, idx) => (
                  <Link key={anime.mal_id} href={`/anime/${anime.mal_id}`} className="flex gap-3 items-center group">
                    <span className="text-lg font-bold text-white/50 group-hover:text-primary transition-colors w-4 text-center">
                      {idx + 2}
                    </span>
                    <div className="relative w-12 h-16 rounded-md overflow-hidden shrink-0">
                      <Image 
                        src={anime.images.jpg.image_url} 
                        alt={anime.title} 
                        fill 
                        className="object-cover group-hover:scale-110 transition-transform"
                      />
                    </div>
                    <div className="flex-1 min-w-0">
                      <h4 className="text-sm font-medium text-gray-100 truncate group-hover:text-primary transition-colors">
                        {anime.title}
                      </h4>
                      <div className="flex items-center gap-2 mt-1">
                        <span className="text-xs text-gray-400 flex items-center gap-1">
                          <Star size={10} className="text-yellow-500 fill-yellow-500" /> {anime.score}
                        </span>
                        <span className="text-[10px] text-gray-500">â€¢ {anime.type}</span>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
            </div>
          </div>
        </section>
      )}

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12 space-y-16">
        
        {/* SEASONAL SECTION */}
        <section>
          <div className="flex items-center justify-between mb-6">
            <div>
              <h2 className="text-2xl md:text-3xl font-bold text-foreground">Season Now</h2>
              <p className="text-muted-foreground text-sm">Anime currently airing this season</p>
            </div>
            <Link href="/seasonal" className="text-sm font-medium text-primary hover:text-primary/80 flex items-center gap-1 transition-colors">
              View All <ArrowRight size={16} />
            </Link>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 md:gap-6">
            {seasonalAnime.map((anime) => (
              <Link key={anime.mal_id} href={`/anime/${anime.mal_id}`}>
                <AnimeCard anime={anime} />
              </Link>
            ))}
          </div>
        </section>

        {/* POPULAR SECTION */}
        <section>
          <div className="flex items-center justify-between mb-6">
            <div>
              <h2 className="text-2xl md:text-3xl font-bold text-foreground">All Time Popular</h2>
              <p className="text-muted-foreground text-sm">Fan favorites highly rated series</p>
            </div>
            <Link href="/popular" className="text-sm font-medium text-primary hover:text-primary/80 flex items-center gap-1 transition-colors">
              View All <ArrowRight size={16} />
            </Link>
          </div>
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-4 md:gap-6">
            {popularAnime.map((anime) => (
              <Link key={anime.mal_id} href={`/anime/${anime.mal_id}`}>
                <AnimeCard anime={anime} />
              </Link>
            ))}
          </div>
        </section>

      </div>
    </main>
  )
}